@import "main-module";

.consultation {
    padding: 60px 0;

    @include below(1024px) {
        padding: 50px  0 30px;
    }
    &__row {
        position: relative;
        padding: 30px;
        background: #404040;
        border-radius: 15px;
        

        @include below(768px) {
            padding: 20px;
            border-radius: 10px;
        }
    }

    &__content {
        position: relative;
        z-index: 100;
        color: $color_white;
        max-width: 630px;

        @include below(768px) {
            max-width: 400px;
        }
    }

    &__title {
        font-weight: 600;
        font-size: 40px;
        line-height: 110%;
        letter-spacing: 0.02em;

        @include below(1240px) {
            font-size: 35px;
        }

        @include below(768px) {
            font-size: 26px;
        }

        br {
            display: none;

            @include below(479px) {
                display: block;
            }
        }
    }

    &__text {
        font-size: 16px;
        line-height: 130%;
        margin: 16px 0 24px;

        a {
            color: $color_white;
            transition: text-decoration .3s linear;

            &:hover {
                text-decoration: underline;
            }
        }

        @include below(768px) {
            margin: 15px 0;
        }

        br {
            display: none;

            @include below(1240px) {
                display: block;
            }
        }
    }

    &__btn {
        position: relative;
        z-index: 1000;
        padding: 12px 20px;
        &:hover {

        }

        @include below(479px) {
            width: 100%;
        }
    }

    &__img {
        position: absolute;
        right: 0;
        bottom: 0;

        @include below(1240px) {
            width: 440px;
        }

        @include below(1024px) {
            display: block;
            position: absolute;
            bottom: 35px;
            right: 0;
            z-index: 1;
            width: 250px;
            max-height: 290px;
        }
        @include below_and_above(768px, 410px) {
            bottom: 6px;
        }


        @include below(370px) {
            opacity: 0.6;
            bottom: 50px;
        }

        img {
            border-bottom-right-radius: 15px;
            object-fit: contain;
            height: 100%;

            @include below(768px) {
                border-bottom-right-radius: 10px;
            }
        }
    }
}