@import "main-module";

.similar-products {
    padding: 40px 0 80px;

    @include below(768px) {
        padding: 30px 0 40px;
    }

    &__container {
        background-color: #F8F8F8;
        padding: 60px 0;

        @include below(768px) {
            padding: 40px 0;
        }
    }

    &__title {
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        margin-bottom: 40px;

        @include below(768px) {
            margin-bottom: 30px;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        margin: 0 -12px;

        @include below(768px) {
            margin: 0 -10px;
        }

        @include below(540px) {
            margin: 0;
        }

        .slick-list {
            @include below(768px) {
                margin-bottom: 30px;
            }
        }

        .slick-dots {
            bottom: -40px;
        }

        .slick-dots button{
            display: none;
        }

        .slick-dots li {
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: #E2E3E5;
            transition: transform .3s linear;

            &.slick-active {
               transform: scale(1.5);
               background-color: $color_blue;
            }
        }
    }

    &__item {
        display: block;
        width: calc(16.666% - 24px);
        margin: 0 12px;

        &:not(:nth-child(-n+6)) {
            display: none;
        }

        @include below(1240px) {
            width: calc(20% - 24px);
            
            &:not(:nth-child(-n+5)) {
                display: none;
            }
        }

        @include below(1024px) {
            width: calc(25% - 24px);

            &:not(:nth-child(-n+4)) {
                display: none;
            }
        }

        @include below(768px) {
            width: calc(33.333% - 20px);
            margin: 0 10px;

            &:not(:nth-child(-n+3)) {
                display: none;
            }
        }

        @include below(540px) {
            width: 200px;

            margin: 0 10px;

            &:not(:nth-child(-n+2)) {
                display: none;
            }
        }
    }

    &__img {
        display: block;
        margin-bottom: 15px;
        img {
            border-radius: 8px;
        }
    }

    &__name {
        display: block;
        color: $color_dark;
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: 0.02em;
    }
}