@import "main-module";

.card-org {
    padding: 30px;
    border-radius: 15px;
    border: 1px solid $color_blue;

    @include below(1024px) {
        padding: 20px;
        border-radius: 10px;
    }
    &_border-grey {
        border: 1px solid $color_grey_light;
    }

    &__map {
        display: none;
    }

    &__flex {
        display: flex;
        position: relative;

        @include below(992px) {
            flex-direction: column;
        }
    }

    &__col {
        width: 50%;
        @include below(992px) {
            width: 100%;
        }
        &:first-of-type {
            padding-right: 24px;
            border-right: 1px solid #ECF7FF;
            @include below(992px) {
                padding: 0 0 24px;
                border-right:  none;
                border-bottom: 1px solid #ECF7FF;
            }
        }

        &:last-of-type {
            padding-left: 24px;

            @include below(992px) {
                padding: 24px 0 0;
            }
        }
    }

    &__head {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;

        &-info {
            @include tooltip-template;
            &::before {
                white-space: nowrap;
                text-align: center;
                width: auto;
            }
        }
    }

    &__head-title {
        display: flex;
        align-items: center;
        font-size: 24px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.48px;
        color: $color_dark;

        svg {
            width: 20px;
            height: 20px;
        }

        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;
        }
    }

    &__head-right {
        display: flex;
        align-items: center;
        svg {
            width: 16px;
            height: 16px;
            flex-shrink: 0;
        }
    }

    &__head-btn {
        display: flex;
        align-items: center;
        margin-left: 20px;
        color: $color_blue;
        font-size: 12px;
        font-weight: 500;
        line-height: 130%;
        letter-spacing: 0.24px;

        svg {
            width: 18px;
            height: 18px;
            margin-right: 8px;
        }

        @include below(479px) {
            display: none;
        }

        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;
        }
    }

    &__address {
        display: flex;
        flex-wrap: wrap;
        margin: -5px -5px 24px;

        @include below(478px) {
            margin: 0 0 24px;
        }
    }

    &__address-item {
        display: flex;
        align-items: center;
        padding: 8px 12px;
        border-radius: 6px;
        border: 1px solid $color_grey_light;
        margin: 5px;

        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;
        }


        @include below(478px) {
            margin: 5px 0;
        }

        span {
            white-space: nowrap;
            font-size: 12px;
            font-weight: 500;
            line-height: 130%;
            letter-spacing: 0.24px;
        }

        svg {
            width: 16px;
            height: 16px;
            margin-left: 8px;
        }
    }

    &__info {
        margin-bottom: 24px;

        @include below(479px) {
            margin-bottom: 0;
        }
    }

    &__info-row {
        display: flex;
        align-items: center;
        @include below(479px) {
            justify-content: space-between;
        }
    }

    &__info-text {
        color: #BBB;
        font-size: 12px;
        font-weight: 400;
        line-height: 130%;
        letter-spacing: 0.24px;
        margin-bottom: 8px;
    }

    &__info-tel {
        font-size: 24px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.48px;  
    }

    &__media {
        margin-left: 20px;
        svg {
            width: 24px;
            height: 24px;

            @include below(479px) {
               width: 32px;
               height: 32px;
               margin: 0;
            }
        }
    }

    &__bottom {
        display: flex;
        @include below(479px) {
            display: none;
        }
    }

    &__bottom-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        &:first-of-type {
            margin-right: 10px;
        }
        svg {
            width: 18px;
            height: 18px;
            margin-right: 10px;
        }
    }

    &__price {
        margin-bottom: 16px;
    }

    &__list {
        display: flex;
        flex-direction: column;
    }

    &__list-title {
        color: $color_grey;
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        margin-bottom: 12px;
    }

    &__list-item {
        display: flex;
        justify-content: space-between;
        transition: background-color .3s linear;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: 0.02em;
        border-radius: 8px;
        
        &:not(:nth-of-type(-n+3)) {
            display: none;
        }
 

        &::after {   
            content: "";
            display: block;
            border-bottom:  1px dashed #E2E3E5;
            margin: 0 10px;
            flex: auto;
            order: 1;
        }
        &:not(:last-of-type) {
            margin-bottom: 12px;
        }
    }

    &__list-link {
        display: inline-flex;
        align-items: center;
        color: $color_blue;
        font-size: 12px;
        &[hidden] {
            display: none;
        }

        svg {
            width: 18px;
            height: 18px;
            transform: rotate(90deg);
        } 
    }

    &__list-text {
        color: $color_blue;

        &_dark {
            color: $color_dark;
        }
    }

    &__list-numb {
        order: 2;
        color: $color_dark;
    }

    &__links-title {
        color: $color_grey;
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        margin-bottom: 12px;
    }

    &__links-row {
        display: flex;
        flex-wrap: wrap;
        margin: -5px;

        &_dark {
            .card-org__link {
                color: $color_dark;
            }
        }
    }

    &__link {
        padding: 8px 12px;
        margin: 5px;
        font-size: 12px;
        font-weight: 500;
        line-height: 130%;
        border-radius: 6px;
        border: 1px solid  $color_light_blue;
        color: $color_blue;
        
        &_dark {
            color: $color_dark;
        }
    }

    &__col-btn {
        display: none;
        align-items: center;
        justify-content: center;
        margin-top: 24px;
        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;
        }
        @include below(479px) {
            width: 100%;
            display: flex;
        }

        &:nth-of-type(2) {
            margin-top: 10px;
        }

        svg {
            width: 18px;
            height: 18px;
            margin-right: 8px;
        }
    }
}