@import "main-module";

.map {
    position: relative;
    margin-bottom: 24px;

    &__container {
        height: 300px;
        overflow: hidden;
        border-radius: 15px;

        @include below(1024px) {
            border-radius: 10px;
        }
    }

    &__btn {
        position: absolute;
        top: 20px;
        left: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 12px 20px;
        z-index: 100;

        @include below(450px) {
            position: static;
            margin-top: 10px;
            width: 100%;
        }

        svg {
            width: 18px;
            height: 18px;
            margin-right: 10px;
        }
    }
}