@import "main-module";

.news-block {
    @include block-padding;

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px; 

        @include below(1024px) {
            margin-bottom: 30px;
        }
    }
    
    &__title {
        @include block-title($isMargin: false);
    }

    &__head-link {
        @include head-link;
        margin-left: 24px;
    }

    &__slider {
        display: flex;
        align-items: stretch;
        margin: 0 -12px;


        @include below(500px) {
            margin: 0;
        }

        .slick-track {
            display: flex;
            align-items: stretch;
            margin: 0;
        }

        &.slick-initialized {
            .slick-slide {
                height: initial;
                display: flex;
                flex-direction: column;
            }
        }
    }
   

    &__item {
        width: calc(25% - 24px);
        margin: 0 12px;
        border: 1px solid #E2E3E5;
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        color: $color_dark;

        &:not(:nth-child(-n+4)) {
            display: none;
        }

        @include below(1240px) {
            width: calc(33.3333% - 24px);
            &:not(:nth-child(-n+3)) {
                display: none;
            }
        }

        @include below(1024px) {
            width: calc(50% - 24px);

            &:not(:nth-child(-n+2)) {
                display: none;
            }
        }

        @include below(768px) {
            border-radius: 10px;
        }

        @include below(500px) {
            width: 70vw;
            margin: 0 10px;

            &:not(:nth-child(1)) {
                display: none;
            }
        }

    }

    &__img {
        
        img {
            width: 100%;
            border-radius: 15px;

            @include below(768px) {
                border-radius: 10px;
            }
        }
    }

    &__body {
        flex: auto;
        display: flex;
        flex-direction: column;
        padding: 20px 16px;
    }

    &__timer {
        display: flex;
        align-items: center;

        svg {
            width: 16px;
            height: 16px;
        }

        span {
            font-size: 14px;
            line-height: 130%;
            color: $color_grey;
            margin-left: 10px;
        }
    }

    &__text {
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        margin: 12px 0 auto;
        display: block;
    }

    &__data {
        display: block;
        font-size: 12px;
        line-height: 130%;
        margin-top: 12px;
        color: $color_grey;
    }

    &__btn {
        display: none;
        text-align: center;
        @include below(479px) {
            display: inline-block;
            width: 100%;
            margin-top: 30px;
        }
    }
}