@import "main-module";

.type {
    padding: 60px 0;

    @include below(1024px) {
        padding: 30px 0;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
        @include below(768px) {
           margin-bottom: 16px;
        }

        &_second {
            .type__title {
                &:nth-child(2) {
                    @include below(768px) {
                        max-width: 160px;
                    }

                    @include below(415px) {
                        max-width: 100px;
                    }
                }
            }
        }
    }

    &__title {
        color: $color_grey;
        font-size: 16px;
        font-weight: 500;
        letter-spacing: 0.32px;
        text-align: start;

        &:first-of-type,
        &:last-of-type {
            width: 100%;
            max-width: 170px;

            @include below(992px) {
                max-width: 120px;
            }

            @include below(768px) {
                max-width: 80px;
            }

            @include below(368px) {
                max-width: 60px;
            }  
        }

        &:nth-child(2) {
            flex: auto;
            margin: 0 24px;

            @include below(768px) {
                margin: 0 15px;
            }

            @include below(479px) {
                width: 100%;
                max-width: 160px;
            }
        }

        &:last-of-type {
            text-align: end;
        }
    }

    &__wrap {
        &_second {
            .type__item {
                .type__text {
                    @include below(768px) {
                        max-width: 160px;
                    }

                    @include below(415px) {
                        max-width: 100px;
                    }
                }
            }
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        padding: 16px 0;
        &:first-child {
            padding: 0 0 16px;
        }
        &:last-child {
            padding: 16px 0 0;
        }
        &:not(:last-of-type) {
            border-bottom: 1px solid $color_grey_light;
        }
    }

    &__col {
        width: 100%;
        max-width: 170px;
        display: flex;
        flex-direction: column;

        @include below(992px) {
            max-width: 120px;
        }

        @include below(768px) {
            max-width: 80px;
        }

        @include below(368px) {
            max-width: 60px;
        }
    }

    &__sort {
        font-size: 16px;
        color: $color_blue;

        &_mt {
            margin-top: 16px;
        }
    }

    &__text {
        flex: auto;
        margin: 0 24px;
        color: $color_grey;
        font-size: 16px;
        text-align: start;

        @include below(768px) {
            margin: 0 15px;
        }

        @include below(479px) {
            width: 100%;
            max-width: 160px;
        }
    }

    &__price {
        font-size: 16px;
        font-weight: 500;
        text-align: end;

        &_mt {
            margin-top: 16px;
        }
    }
}