@import "main-module";

.intro-block {

    padding: 60px 0;
    
    @include below(1024px) {
        padding: 30px 0;
    }

    &__wrap {
        position: relative;
    }

    

    &__content {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 40px;

        @include below(991px) {
            position: relative;
            z-index: 100;
            padding: 0;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 56px;
        line-height: 110%;
        letter-spacing: 0.04em;
        max-width: 720px;
        margin-bottom: 60px;
        span {
            color: $color_blue;
        }

        @include below(1200px) {
            font-size: 36px;
        }

        @include below(991px) {
            padding: 20px;
            margin: 0;
            position: absolute;
            left: 0;
            bottom: 105%;
        }
    }

    &__slider {
        background: #ecf7ff;
        border-radius: 30px;
        overflow: hidden;
        @include below(991px) {
            border-radius: 10px;
            
        } 
    }


    &__img {
        height: 607px;
        background-color: #ecf7ff;

        &.slick-slide {
            height: 607px;
        }

        @include below(991px) {
        
            height: 440px;
            &.slick-slide {
                height: 440px;
            }
        } 

        &:not(:first-child) {
            display: none;
        }

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__row {
        flex-wrap: wrap;
        display: flex;
        align-items: stretch;
        margin: -12px;

        @include below(991px) {
            padding-top: 10px;
        }

        @include below(520px) {
            margin: -5px;
        }
    }

    &__item {
        position: relative;
        width: calc(25% - 24px);
        margin: 12px;
        padding: 25px 25px 30px 30px;
        border: 1px solid #404040;
        border-radius: 15px;
        color: $color_dark;
        transition: background-color .3s linear,
        color .3s linear, border-color .3s linear;

        display: flex;
        flex-direction: column;

    

        @include below(991px) {
            width: calc(50% - 24px);
        }

        @include below(520px) {
            padding: 20px 5px;
            margin: 5px;
            width: calc(50% - 10px);
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &.is-active,
        &:hover {
            background-color: $color_blue;
            color: $color_white;
            border-color: $color_blue;
            
            svg { 
                stroke: #fff;
            }
        }

        &:active {
            background-color: $color_blue_hover;
            border-color: $color_blue_hover;
        }
        
        &-text {
            font-weight: 600;
            font-size: 24px;
            line-height: 110%;
            letter-spacing: 0.02em;
            display: block;  
            margin-top: 30px;

            @include below(1200px)  {
                // margin-top: ;
                font-size: 16px;

            }

            @include below(520px) {
                margin-top: 0;
                text-align: center;
            }
        }

        &-more {
            margin-left: auto;
            margin-bottom: auto;
            display: flex;
            align-items: center;
            
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            letter-spacing: 0.02em;

            @include below(520px) {
                display: none;
            }

            svg {
                width: 18px;
                height: 18px;
                stroke: #404040;
                transition: stroke .3s linear;
            }
        }
    }
}