@import "main-module";

.services {
    margin-top: 24px;
    border-radius: 10px;

    @include below(992px) {
        margin-top: 60px;
    }

    &__wrap {
        position: relative;
        border-radius: 10px;
        padding: 20px;
    }

    &__title {
        font-size: 18px;
        font-weight: 500;
        line-height: 130%
    }

    &__link {
        display: flex;
        align-items: center;
        margin-top: 40px;
        svg {
            width: 18px;
            height: 18px;
        }
    }

    &__content {
        position: relative;
        z-index: 100;
    }

    &__img {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        

        img {
            border-radius: 10px;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }


}