@import "main-module";

.form-filter {
    padding-top: 8px;
    margin-bottom: 32px;

    @include below(1024px) {
        padding-top: 6px;
        margin-bottom: 30px;
    }

    &__wrap {
        position: relative;
        background-color: $color_grey_light2;
        padding: 30px;
        border-radius: 15px;
        border: 1px solid $color_grey_light;

        @include below(768px) {
            padding: 20px;
            border-radius: 10px;

            @include below(440px) {
                padding: 4px 20px;
            }
        }
    }


    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include below(440px) {
            width: 100%;
            padding: 16px 0;
            cursor: pointer;
            
            &.is-show {
                .form-filter__arrow svg {
                    transform: rotate(180deg);
                }

                & ~ .form-filter__form {
                    display: block;
                }
                & ~ .form-filter__reset {
                    display: block;
                }
            }
        }
    }

    &__title {
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: 0.02em;
    }

    &__badge {
        font-weight: 500;
        font-size: 12px;
        line-height: 110%;
        padding: 2px 6px;
        color: $color_blue;
        background-color: $color_light_blue;
        border-radius: 4px;
    
        @include above(441px) {
            display: none;
        }
    }

    &__arrow {
        margin: -4px 0 -4px 16px;
        @include above(441px) {
            display: none;
        }

        svg {
            width: 24px;
            height: 24px;
            transition: transform $trns;
        }
    }

    &__reset {
        top: 0;
        right: 0;
        font-weight: 500;
        font-size: 12px;
        line-height: 130%;
        letter-spacing: 0.02em;

        color: $color_red;
        margin-left: auto;

    
        &:hover:not(:active) {
            color: $color_red_hover;
        }

        @include below(440px) {
            span {
                display: none;
            }
            display: flex;
            justify-content: center;
            align-items: center;
            height: 20px;
            width: 20px;
        }

        svg {
            width: 20px;
            height: 20px;
            @include above(441px) {
                display: none;
            }
        }
    }

   

 

    &__form {
        width: 100%;
        padding-top: 16px;

        @include below(440px) {
            display: none;
            padding-bottom: 16px;
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: -5px;
        @include below(440px) {
            margin: -10px 0 0;
        }

        &_second {
            align-items: center;
            flex-wrap: nowrap;
            margin: -8px;
        }
    }

    &__label {
        position: relative;
        margin: 5px;
        width: calc(25% - 10px);
        @include below(992px) {
            width: calc(50% - 10px);
        }

        @include below(440px) {
            width: 100%;
            margin: 10px 0 0;
        }
    }


    &__search {
        width: 100%;
        border: 1px solid $color_grey_light;
        border-radius: 8px;
        height: 48px;
        display: flex;
        align-items: center;
        transition: border-color $trns;
        padding: 0 20px;
        background-color: $color_white;
        font-size: 14px;
        line-height: 130%;
        font-family: $accentFont;
        color: $color_grey;

        &:hover {
            border-color: $color_blue;
        }
    }

    &__icon {
        position: absolute;
        top: 12px;
        right: 12px;
        svg {
            width: 24px;
            height: 24px;
           
        }
    }

}