@import "main-module";

.buyer {
    @include block-padding;
    
    &__main-title {
       @include block-title;
    }

    &__grid {
        display: flex;

        @include below(991px) {
            display: block;
        }
    }

    &__img {
        z-index: 1;
        img {
            height: 100%;
            object-fit: cover;
        }
    }

    &__right {
        @include below(1200px) {
            width: 50%;
        }

        @include below(991px) {
            width: 100%;
        }
    }

    &__bottom {
        display: flex;
        margin: 0 -12px;
        @include below(991px) {
            margin: 0 -10px;
        }
        .buyer__item.buyer__item_last {
            margin: 0 12px;
            width: calc(50% - 24px);
            @include below(991px) {
                margin: 0 10px;
                width: calc(50% - 20px);
            }
        }
    }

    &__item {
        position: relative;
        padding: 30px;
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        color: $color_white;
       

        @include below(991px) {
            padding: 20px;
            border-radius: 10px;
        }

        &.buyer__item_first {
            max-width: 648px;
            height: 448px;
            background: linear-gradient(180deg, rgba(113,204,255,0.4318102240896359) 0%, rgba(48,180,255,1) 100%);
            overflow: hidden;
            margin-right: 24px;

            @include below(991px) {
                max-width: 100%;
                height: 360px;
                margin-right: 0;
                margin-bottom: 20px;
            }

            &:hover {
                img {
                    transform: scale(1.1);
                }
            }

            .buyer__img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
                img {
                    border-radius: 15px;
                    height: 100%;
                    width: 100%;
                    transition: transform $trns;

                    @include below(991px) {
                        border-radius: 10px;
                    }
                }
            }

            .buyer__title {
                font-size: 40px;
                margin-bottom: 30px;
                margin-top: auto;

                @include below(1024px) {
                   font-size: 30px;
                }

                @include below(991px) {
                    font-size: 26px;
                    margin-bottom: 16px;
                }
            }
        }

        &.buyer__item_second {
            width: 100%;
            height: 212px;
            justify-content: space-between;
            background: #F8F8F8;
            margin-bottom: 24px;
            overflow: hidden;
            &:hover {
                img {
                    transform: scale(1.1);
                }
            }

            @include below(991px) {
                height: 210px;
                margin-bottom: 20px;
            }

            .buyer__text {
                color: #404040;
            }

            .buyer__title {
                margin-bottom: 30px;
                color: #404040;
                @include below(991px) {
                     margin-bottom: 16px;
                }

                br {
                    display: none;
                    @include below(991px) {
                        display: block;
                    }
                }
            }

            .buyer__link {
                color: $color_dark;
                svg {
                    stroke: #404040;
                }
            }

            .buyer__img {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;

                img {
                    width: 100%;
                    transition: transform $trns;
                    @include below(991px) {
                        height: 62%;
                    }
                }

                @include below(1024px) {
                    display: block;
                   
                    img {
                        border-top-right-radius: 10px;
                    }
                }
            }
        }

        &.buyer__item_last {
            background: radial-gradient(44.16% 53.82% at 17.31% 58.41%, #545454 0%, #404040 100%);
            
            &:hover {
                background: radial-gradient(44.16% 53.82% at 17.31% 58.41%, #494949 1.22%, #383838 100%, #353535 100%)
            }

            height: 212px;

            @include below(991px) {
                height: 150px;
            }

            @include below(768px) {
                height: auto;
                align-items: center;
                justify-content: baseline;
                height: 58px;
                padding: 20px 10px;
            }

            .buyer__title {
                text-align: left;
                color: $color_white;

                
                @include below(768px) {
                    text-align: center;
                    font-size: 16px;
                    span {
                        display: none;
                        font-size: 20px;
                    }
                }
            }

            .buyer__link {
                @include below(768px) {
                    display: none;
                }
            }
        }
    }

    &__title {
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: 0.02em;
        @include below(1200px) {
            font-size: 18px;
        }

        @include below(991px) {
            font-size: 20px;
        }
    }

    &__text {
        font-size: 16px;
        line-height: 130%;

        br {
            &:last-of-type {

                @include below_and_above(1350px, 769px) {
                    display: block;
                }
            }

            @include below(768px) {
                display: none;
            }
        }
    }

    &__btn {
        margin-top: 30px;
        text-align: center;
        @include below(991px) {
            margin-top: 24px;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        position: relative;
        z-index: 10;
        // position: absolute;
        // top: 20px;
        // right: 20px;
        font-size: 12px;
        line-height: 130%;
        letter-spacing: 0.02em;
        color: $color_white;
        margin: 0 0 auto auto;

        svg {
            width: 18px;
            height: 18px;
        }
    }
}

