@import "main-module";

.materials {
    padding-bottom: 120px;

    @include below(1024px) {
        padding-bottom: 60px;
    }

    @include below(768px) {
        padding-bottom: 20px;
    }


    &__grid {
        @include above(1200px) {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            align-items: stretch;
            margin: -12px;
        }
    }

    &__card-name {
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: 0.02em;
        margin-bottom: 8px;

        @include below(768px) {
            font-size: 20px;
        }
    }

    &__card-code {
        font-size: 12px;
        line-height: 130%;
    }

    &__card {
        padding: 30px;
        border-radius: 15px;
        border: 1px solid $color_grey_light;
        padding: 30px;

        

        display: flex;
        flex-direction: column;

        @include above(1200px) {
            width: calc(50% - 24px);
            margin: 12px;
        }

        @include below(1199px) {
            width: 100%;
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }

        @include below(768px) {
            padding: 20px;
        }
    }

    &__card-head {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: auto;
        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__card-links {

        display: flex;
        align-items: center;
        margin-left: 24px;
        @include below(768px) {
            margin-left: 16px;
        }
    }

    &__card-link {
        width: 24px;
        height: 24px;
        color: $color_blue;
        transition: color $trns;
        position: relative;

        &:hover {
            &::after,
            &::before {
              opacity: 1;
              visibility: visible;
            }
        }

        &:hover:not(:active) {
            color: $color_blue_hover;
        }
        &:not(:last-child) {
            margin-right: 20px;
        }

        &[data-tooltip] {
            &::before {
                content: attr(data-tooltip);
            }
            &::after {
                content: "";
            }
        }  
        
        &::before,
        &::after {
            position: absolute;
            top: 0;
            left: 50%;
            color: $color_white;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.2s linear;
            font-size: 12px;
        }

        &::before {
            background-color: rgba(0, 0, 0, 0.8);
            border-radius: 0.5em;
            width: 10rem;
            padding: 0.5rem;
            transform: translate(-50%, calc(-100% + 12px * -1));
        }
        
        &::after {
            
            width: 12px;
            aspect-ratio: 1;
            background-color: rgba(0, 0, 0, 0.8);
            clip-path: polygon(44% 100%, 0 44%, 100% 44%);
            transform: translate(-50%, calc(-100% + 12px / 2 * -1));
        }
    }

    &__card-list {
        column-count: 2;
        column-gap: 12px;
        margin-top: 24px;
        margin-bottom: auto;

        @include below(768px) {
            columns: initial;
        }
    }

    &__list-icon {
        width: 16px;
        height: 16px;
        color: $color_blue;
        margin-right: 8px;
    }

    &__list-item {
        display: flex;
        flex-wrap: wrap;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: 0.02em;
        margin-bottom: 12px;
    }

    &__list-val {
        color: $color_grey;
        margin-right: 8px;
    }

    &__card-foot {
        margin-top: 8px;
        border-top: 1px solid $color_grey_light;
        padding-top: 20px;
        

        @include below(768px) {
            padding-top: 16px;
            margin-top: 4px;
        }

        @include above(441px) {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__cost {
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;

        @include below(768px) {
            margin-bottom: 20px;
        }
    }

    &__btn {
        width: 100%;
        max-width: 168px;

        @include below(440px) {
            max-width: 100%;
        }
    }

    &__show-more {
        font-weight: 500;
        font-size: 12px;
        line-height: 130%;
        margin-bottom: 12px;
        
        button {
            color: $color_blue;
            transition: color $trns;
            &:hover:not(:active) {
                color: $color_blue_hover;
            }
        }
    }
}