@import "main-module";

.scrap-categories {
    padding: 60px 0px;
    overflow: hidden;
    @include below(1024px) {
        padding: 30px 0;
    }

    h2 {
        font-size: 18px;
        font-weight: 500;
        line-height: 130%; 
        margin-bottom: 16px;
    }

    p {
        color: #494949;
        font-size: 16px;
        line-height: 130%;
        &:not(:last-of-type) {
            margin-bottom: 24px;
        }
    }
    
    &__row {
        display: flex;
        margin: 0 -12px 16px;

        @include below(550px) {
            flex-wrap: wrap;
            margin: -20px -10px 16px;
        }
    }

    &__item {
        position: relative;
        width: 100%;
        max-width: 160px;
        margin: 0 12px;

        @include below(550px) {
            margin: 20px 10px 0;
        }
    }

    &__icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        svg {
            width: 40px;
            height: 40px;
        }
    }

    &__img {
       
        img {
            width: 100%;
            height: auto;
            border-radius: 10px;
        }
    }

    &__name {
        font-size: 12px;
        line-height: 130%; 
        letter-spacing: 0.24px;
        color: $color_dark;
        margin-top: 12px;
    }
}