@import "main-module";

.also {

    &__title {
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        margin-bottom: 24px;
    }

    &__list {
        column-count: 2;
        column-gap: 20px;

        @include below(1400px) {
            column-count: 1;
            column-gap: 0;
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: 0.28px;
        margin-bottom: 12px;

        &:nth-of-type(4) {
            margin-bottom: 0; 
            @include below_and_above(1400px, 993px) {
                margin-bottom: 12px;
            }
        }
        &:nth-of-type(8) {
            margin-bottom: 0;
        }

        @include below(992px) {
            &:not(:nth-child(-n+4)) {
                display: none;
            }
        }
        
        &:after {
            content: "";
            display: block;
            border-bottom: 1px dashed #E2E3E5;
            margin: 0 10px;
            flex: auto;
            order: 1;
        }
    }

    &__item-text {
        color: $color_blue;
    }

    &__item-numb {
        color: $color_dark;
        order: 2;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: -5px;
    }

    &__row-link {
        padding: 12px 20px;
        border-radius: 6px;
        border: 1px solid $color_light_blue;
        margin: 5px;
        font-size: 14px;
    }

    &__btn {
        margin-top: 24px;
        padding: 12px 20px;

        @include below(479px) {
            width: 100%;
        }
    }
}