@import "main-module";

.price {
    padding-bottom: 30px;
    &__flex {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -12px;

        @include below(992px) {
            margin: 0;
        }
    }

    &__col {
        margin: 0 12px;
        &:first-of-type {
            border-radius: 10px;
            padding: 30px;
            border: 1px solid $color_grey_light;
            width: calc(57% - 24px);

            @include below(1400px) {
                width: calc(50% - 24px);
                padding: 20px;
            }

            @include below(992px) {
                width: 100%;
                margin: 0 0 60px;
            }
        }

        &:last-of-type {
            width: calc(43% - 24px);
            @include below(1400px) {
                width: calc(50% - 24px);
            }

            @include below(992px) {
                width: 100%;
                margin: 0;
            }
        } 
    }

    &__head {
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid $color_light_blue;

        &-text {
            font-size: 14px;
            font-weight: 400;
            line-height: 130%; 
            color: $color_grey;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        font-size: 24px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.48px;

        @include below(1400px) {
            flex-direction: column;
            margin-bottom: 16px;
        }

        &-title {
            font-weight: 500;
            @include below(1200px) {
                font-size: 20px;
            }
        }

        &-numb {
            color: $color_blue;

            @include below(1400px) {
                margin-top: 16px;
            }
        }
    }

    &__company {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;

        &-item {
            display: flex;
            align-items: center;
            
            @include tooltip-template;
            &::before {
                white-space: nowrap;
                text-align: center;
                width: auto;
            }

            svg {
                flex-shrink: 0;
                width: 18px;
                height: 18px;
                margin-left: 10px;
            }
        }

        &-title {
            font-size: 14px;
            font-weight: 500;
            line-height: 130%;
        }

        &-icon {
            margin-right: 20px;
            svg {
                width: 16px;
                height: 16px;
            }
        
            @include tooltip-template;
            &::before {
                white-space: nowrap;
                text-align: center;
                width: auto;
            }
        }

        &-btn {
            display: flex;
            align-items: center;
            font-size: 12px;
            font-weight: 500;
            line-height: 130%;
            letter-spacing: 0.24px;
            color: $color_blue;
            svg {
                width: 18px;
                height: 18px;
                margin-right: 8px;
            }
        }
    }

    &__alert {
        display: flex;
        align-items: center;
        padding: 12px 20px;
        border-radius: 8px;
        border: 1px solid #B76E00;
        background-color: #FFFBE6;
        margin-bottom: 24px;

        @include below(479px) {
            justify-content: space-between;
        }

        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;
        }

        span {
            color: #B76E00;
            font-size: 14px;
            font-weight: 500;
            line-height: 130%;
        }

        svg {
            width: 16px;
            height: 16px;
            margin-left: 10px;
        }
    }


    &__title {
        font-size: 14px;
        font-weight: 500;
    }

    &__info {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;

        &_second {
            @include below(1400px) {
                flex-direction: column;
            }
        }

        @include below(1200px) {
            flex-direction: column;
        }

        &-col {
            &:last-of-type {
                display: flex;
                align-items: flex-end;
                position: relative;

                @include below(1200px) {
                   margin-top: 24px;
                }
            }

            &_second {
                display: flex;
                flex-wrap: wrap;

                @include below(992px) {
                   justify-content: space-between;
                }
            }
        }

        &-top {
            margin-bottom: 24px;
            &_second {
                margin: 0 20px 0 0;
                @include below(1200px) {
                    width: 100%;
                    margin: 0 0 24px;
                }
            }
        }

        &-head {
            display: flex;
            align-items: center;
            margin-bottom: 8px;
            &-text {
                color: #BBB;
                font-size: 12px;
            }

            &-icon {
                margin-left: 10px;
                display: none;
                @include below(479px) {
                    display: block;
                }

                svg {
                    width: 18px;
                    height: 18px;
                }
            }

            &_active {
                .price__info-head-icon {
                    svg {
                        transform: rotate(180deg);
                    }
                }
            }
        }

        &-content {
            transition: .3s linear;
            
            @include below(479px) {
                max-height: 0;
                overflow: hidden;
                opacity: 0;
            }
        }

        &-text {
            color: #BBB;
            font-size: 12px;
            margin-bottom: 8px;
        }

        &-row {
            display: flex;
            align-items: center;
    
            &:not(:last-of-type) {
                margin-bottom: 8px;
            }

            @include below(479px) {
                justify-content: space-between;
            }
        }

        &-tel {
            margin-right: 20px;
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 0.28px;
            color: $color_dark;
            &_blue {
                font-size: 24px;
                font-weight: 600;
                line-height: 110%;
                letter-spacing: 0.48px;
                color: $color_blue;

                @include below(374px) {
                    font-size: 20px;
                }
            }
        }

        &-bottom {
            @include below(479px) {
                width: 100%;
            }

            .price__info-text {
                @include below(479px) {
                    display: none;
                }
            }
        }

        &-media {
            display: flex;
            align-items: center;
            margin: 0 -2px;

            a {
                margin: 0 2px;
                svg {
                    width: 18px;
                    height: 18px;

                    
                    @include below(992px) {
                        width: 32px;
                        height: 32px;
                    }
                }
            }

            &_first {      
                a {
                    margin: 0 2px;
                    svg {
                        width: 24px;
                        height: 24px;
                    }

                    @include below(992px) {
                        width: 32px;
                        height: 32px;
                    }
                }
            }
        }

        &-icon {
            margin-right: 10px;
            svg {
                width: 42px;
                height: 42px;
            }
        }

        &-support {
            &-row {
                display: flex;
            }

            &-text {
                color: #BBB;
                font-size: 12px;
                letter-spacing: 0.24px;
            }

            &-icon {
                margin-left: 10px;
                @include tooltip-template;
                &::before {
                    white-space: nowrap;
                    text-align: center;
                    width: auto;

                    @include below(600px) {
                        left: 20%;
                    }
                }
                svg {
                    width: 16px;
                    height: 16px;
                }

                @include below(479px) {
                    position: absolute;
                    top: 12px;
                    right: 0;
                }
            }

            &-tel {
                color: $color_dark;
                font-size: 14px;
                font-weight: 500;
                line-height: 130%; 
                letter-spacing: 0.28px;
            }
        }
    }


    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(1400px) {
            flex-direction: column;
            align-items: flex-start;
        }

        &-btn {
            @include below(479px) {
                width: 100%;
            }
        }

        &-opinion {
            display: flex; 
            align-items: center;
            @include below(1400px) {
                margin-top: 24px;
            }

            @include below(479px) {
                width: 100%;
                justify-content: space-between;
            }
        }
        &-left {
            display: flex;
            align-items: center;
            margin-right: 30px;
        }

        &-icon{
            margin: 5px 10px 0 0;
            @include tooltip-template;
                &::before {
                    white-space: nowrap;
                    text-align: center;
                    width: auto;

                    @include below(600px) {
                        left: 20%;
                    }
                }
            svg {
                width: 16px;
                height: 16px;
            }
        }

        &-text {
            font-size: 14px;
            line-height: 130%; 
            letter-spacing: 0.28px;
        }

        &-right {
            display: flex;

            &-btn {
                &:first-of-type {
                    padding-right: 16px;
                    border-right: 1px solid $color_blue;
                }
        
                &:last-of-type {
                    padding-left: 16px;
                }
                svg {
                    width: 20px;
                    height: 20px;
                }
            }
        }

    }
}