// @import "main-module";

:root {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
}

html {
    font-feature-settings: 'ordn' on;

}

*, *::after, *::before {
    box-sizing: border-box;
    font-kerning: none;
    font-feature-settings: 'ordn' on;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    -webkit-text-size-adjust: 100%;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}

body {
    font-family: $accentFont;
    font-size: 16px;
    line-height: 130%;

}

button {
    cursor: pointer;
    padding: 0;
    border: 0;
}

a {
    cursor: pointer;
    text-decoration: none;
    color: #30B4FF;
}

.container {
    width: 100%;
    margin: 0 auto;
    padding-right: calc(var(--bs-gutter-x) * .5);
    padding-left: calc(var(--bs-gutter-x) * .5);

    @include above(1400px) {
        max-width: 1320px;
    }

    @include above(1200px) {
        max-width: 1140px;
    }

    @include above(992px) {
        max-width: 960px;
    }

    @include above(768px) {
        max-width: 720px;
    }
    
    @include above(576px) {
        max-width: 540px;
    }
}

ul {
  list-style-type: none;
}

:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}

::selection {
    color: #fff;
    background: #30B4FF;
}


input,
button,
textarea,
fieldset {
  background: transparent;
  border: 0;
  padding: 0;
}

img {
    max-width: 100%;
    height: auto;
}

.title {
    font-family: $accentFont;
    font-weight: 500;
    font-size: 32px;
    line-height: 110%;
    margin-bottom: 24px;
    letter-spacing: 0.01em;
}

.text {
    font-size: 16px;
    line-height: 130%;
    &:not(:last-child) {
        margin-bottom: 24px; 
    }
}