@import "main-module";

.cards {
    padding-bottom: 120px;

    &_pb {
        padding-bottom: 112px;
    }

    @include below(768px) {
        padding-bottom: 60px;
    }

    &_modal {
        padding: 0;
    }
    
    &__wrap {
        display: flex;
        flex-direction: column;

        &_second {
            .cards__card {
                &:not(:last-of-type) {
                    margin-bottom: 20px;
                }
            }
        }
    }

    &__card {
        &:not(:last-of-type) {
            margin-bottom: 24px;
        }
    }

    &__tab {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 32px;

        &-list {
            display: flex;
            align-items: center;
            flex: auto;
        }

        &-btn {
            color: $color_dark;
            border-color: #30B4FF;
            transition: color .3s linear, background-color .3s linear;

            @include below(500px) {
                width: 32%;
            }

            &:not(:last-child) {
                margin-right: 10px;
            }
            &.cards__tab-btn_active {
                background-color: #30B4FF;
                color: #fff;

                &:hover {
                    color: #fff;
                }
            }
        }

        &-add {
            display: flex;
            align-items: center;
            @include below(500px) {
                display: none;
            } 
            svg {
                width: 18px;
                height: 18px;
                margin-right: 10px;
            }
        }
    }
}