@import "main-module";

.breadcrumbs {
    &__list {
        display: flex;
        flex-wrap: wrap;
        padding-top: 15px;
        padding-bottom: 15px;
        margin-bottom: 25px;

        @include below(1024px) {
            padding-top: 8px;
            padding-bottom: 8px;
        }
    }

    &__item {
        padding: 3px 0;
        font-size: 14px;
        line-height: 18px;
        color: $color_grey;
       
        &:not(:last-child) {
            margin-right: 4px;
            a::after {
                content: "/";
                margin-left: 4px;
            }
        }
       
        a {
            display: flex;
            align-items: center;
            transition: color $trns;
            color: inherit;

            &:hover:not(:active) {
                color: $color_blue;
            }
        }
    }
}