@import "main-module";

.select {
    width: 100%;
    appearance: none;
}

.select {
    border: 1px solid $color_grey_light;
    border-radius: 8px;
    height: 48px;
    display: flex;
    align-items: center;
    transition: border-color $trns;
    padding: 0 20px;
    &[multiple] {
        overflow: hidden;
        option {
            opacity: 0;
        }
    }
}

.select2-dropdown--above {
    .select2-search__field {
        margin-top: 16px;
    }
}

.select2-container--above {
    .customize-select[aria-expanded=true] {
        border-top-color: transparent !important;
        border-bottom-color: $color_blue !important;
    }
}

.select2-container--below {
    .customize-select[aria-expanded=true] {
        border-top-color: $color_blue !important;
        border-bottom-color: transparent !important;
    }
}

.select2-container--default {
    &.select2-container--focus .select2-selection--multiple.customize-select {
        border-color: $color_grey_light;
    }

    .select2-selection--multiple.customize-select {

        .select2-selection__choice__remove {
            display: none;
        }

        &.select2-selection--clearable {
            .select2-search {
                display: none !important;
            }
        }

        .select2-selection__clear {
            margin: 0;
            right: 19px;
        }
        
        .select2-selection__rendered {
            display: flex;
            align-items: center;
            margin-left: -5px;
        }

        .select2-search {
            font-size: 14px;
            line-height: 130%;
            font-family: $accentFont;
            color: $color_grey;
        }

        .open-select-more {
            font-size: 12px;
            line-height: 130%;
            color: $color_blue;

            border-radius: 4px;
            background-color: $color_light_blue;
            padding: 4px 6px;
            display: inline-block;
            margin-left: 5px;
            margin-top: 5px;
        }

        .select2-selection__choice {
            border-radius: 4px;
            background-color: $color_light_blue;
            padding: 4px 6px;
            border: 0;

            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: $color_blue;
            font-family: $accentFont;
        }

        .select2-selection__choice__remove {
            position: static;
            padding: 0;
            
            border-right-color: $color_blue;
            color: $color_blue;
            padding-right: 2px;
            margin-right: 2px;
        }
    }

   

    .select2-selection--multiple.customize-select,
    .select2-selection--single.customize-select {
        border: 1px solid $color_grey_light;
        border-radius: 8px;
        height: 48px;
        display: flex;
        align-items: center;
        transition: border-color $trns;
        
        &[aria-disabled="true"] {
            background: transparent;   

            .select2-selection__arrow {
                opacity: 0.3;
            }
        }
    
        &[aria-expanded="true"]:not([aria-disabled="true"]),
        &:hover:not([aria-disabled="true"]) {
            border-color: $color_blue !important;
            
        }

        &[aria-expanded="true"] {
            border-radius: 8px !important;
    
            .select2-arrow svg {
                transform: rotate(180deg);
            }
        }
    
        &.select2-selection--clearable {
            .select2-arrow {
                display: none;
            }
    
            .select2-selection__rendered {
                color: $color_dark;
            }
        }
    
        &.select2-container--open {
            background: red;
        }
    
        .select2-selection__rendered {
            font-size: 14px;
            line-height: 130%;
            color: $color_grey;
            padding-left: 20px;
        }

        .select2-selection__placeholder {
            font-size: 14px;
                line-height: 130%;
                font-family: $accentFont;
                color: $color_grey;
        }
    
        &.select2-container--open {
            background: red;
        }

        &.customize-select {
            display: flex;
            align-items: center;
        }
    
        .select2-arrow {
            width: 24px;
            height: 24px;
            margin-left: auto;
            margin-right: 10px;
            margin-bottom: -3px;

            &.is-single {
                margin-right: 13px;
            }

            svg {
                width: 100%;
                height: 100%;
                transition: transform $trns;
            }
        }

        .select2-selection__arrow {
            top: 50%;
            right: 15px;
            transform: translateY(-50%);
            
            background-image: url(../img/sprites/select-arrow.svg);
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
    
            width: 24px;    
            height: 24px;
            transition: transform $trns;
            b {
                display: none;
            }
        }
    
        .select2-selection__clear {
            order: 2;
            margin-left: auto;
            font-weight: 300;
            font-size: 20px;
            height: auto;
            color: $color_blue;
        }
    }
    
}

.select2-dropdown.customize-dropdown {
    border-radius: 8px !important;
    border: 0 !important;

    margin-top: 2px; 
    box-shadow: 0px 4px 15px rgba(64, 64, 64, 0.15);
    .select2-search {
        display: none;
    }

    .select2-search.select2-search--dropdown {
        padding: 0 15px;
        input {
            border-radius: 5px;
            border: 1px solid $color_grey_light;
        }
    }

    .select2-results {
        padding: 10px 0;
    }

    .select2-results__options {

    }

    .select2-results__options[aria-multiselectable="true"] {
        .select2-results__option {
            display: flex;
            align-items: center;
            &.select2-results__option--selected {
                &::before {
                    border-color: $color_blue;
                    color: $color_blue;
                }
            }

            &.select2-results__option--selected {
               &::before {
                    background-image: url(../img/sprites/checkbox.svg);
                    background-color: $color_blue;
                    border-color: $color_blue;
               }
            }


            &::before {
                content: "";
                
                background-position: center;
                background-repeat: no-repeat;
                background-size: 6px 6px;
                width: 14px;
                height: 14px;
                display: inline-flex;
                align-items: center;
                justify-content: center;

                border: 2px solid $color_grey;
                font-size: 10px;
                margin-right: 8px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: transparent;
                border-radius: 2px;
            }
        }
    }

    &:not(.is-multiple) {
        .select2-results__option--selectable {
            &.select2-results__option--selected {    
                background-image: url(../img/sprites/check-icon.svg);
                background-size: 24px 24px;
                background-position: 95%;
                background-repeat: no-repeat;
            }
        }
    }

    .select2-results__option--selectable {
        &.select2-results__option--selected {
            background-color: transparent;
        }
    }
    
    .select2-results__option {
        font-size: 14px;
        line-height: 135%;
        padding: 12px 20px;
    }

    .select2-results__option--highlighted.select2-results__option--selectable {
        background-color: $color_light_blue;
        color: $color_blue;
    }
}