@import "main-module";

.section {

    &__head {
        display: flex;
        align-items: center;
        margin-bottom: 32px;

        @include below(768px) {
            flex-direction: column-reverse;
            align-items: flex-start;
        }

        .section__title {
            margin: 0;
        }
    }

    &__title {
        font-family: $accentFont;
        font-weight: 500;
        font-size: 32px;
        line-height: 110%;
        margin-bottom: 24px;
        letter-spacing: 0.01em;

        @include below(1024px) {
            font-size: 24px;
        }

        &_second {
            width: 50%;
            @include below(768px) {
                width: 100%;
            }
        }
    }   
    
    &__text {
        font-family: $accentFont;
        font-size: 16px;
        line-height: 130%;
        &:not(:last-child) {
            margin-bottom: 24px; 
        }
    }

    &__head-right {
        display: flex;
        justify-content: space-between;
        margin-left: 24px;
        
        &_first {
            width: 50%;

            @include below(768px) {
                width: 100%;
                margin: 0 0 20px;
            }
        }

        &_second {
           width: 27%;

            @include below(1400px) {
                width: 65%;
            }

            @include below(768px) {
                width: 100%;
                margin: 0 0 20px;
            }
        }
    }

    &__link {
        display: flex;
        align-items: center;
        color: $color_blue;
        font-size: 12px;
        font-weight: 500;
        line-height: 130%;
        letter-spacing: 0.24px;
        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;

            @include below(600px) {
                left: 20%;
            }
        }
        svg {
            width: 16px;
            height: 16px;
            margin-right: 8px;
        }
    }
}