@import "main-module";
.analysis {
    padding: 0px 0 30px;

    &__wrap {
        padding: 30px;
        background-color: #F8F8F8;
        border: 1px solid #E2E3E5;
        border-radius: 15px;

        @include below(768px) {
            padding: 20px;
            border-radius: 10px;
        }
    }

    &__row {
        display: flex;
        margin: 0 -15px;

        @include below(900px) {
            flex-wrap: wrap;
            margin: 0;
        }
    }

    &__item {
        margin: 0 15px;

        @include below(900px) {
            margin: 0;
        }

        &:first-of-type {
            width: 100%;
            max-width: 180px;

            @include below(400px) {
                max-width: 100%;
                position: relative;
            }
        }

        &:last-of-type {
            width: 100%;
            max-width: 1050px;
            display: flex;
            flex-wrap: wrap;

            @include below(900px) {
                margin: 15px 0 0;
            }
        }
    }

    &__img {
        margin-bottom: 20px;

        @include below(400px) {
            margin-bottom: 0;
        }

        img {
            width: 100%;
            border-radius: 8px;
        }
    }

    &__item-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;

        @include below(400px) {
            width: 90%;
            margin: 0 auto;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 20px;
            border-color: transparent;
            z-index: 10;
        }
       
        svg {
            width: 18px;
            height: 18px;
            margin-right: 5px;
        }
        
    }

    &__title {
        font-weight: 500;
        font-size: 32px;
        line-height: 110%;
        letter-spacing: 0.01em;

        @include below(1024px) {
            font-size: 24px;
        }
    }

    &__center {
        margin-top: 25px;
        @include below(1024px) {
            order: 2;
            margin: 16px 0;
        }
    }
    
    &__text {
        font-size: 16px;
        line-height: 130%;
        margin: 20px 0;

        @include below(1024px) {
            margin: 16px 0;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 12px;
        line-height: 130%;
        letter-spacing: 0.02em;
        transition: color $trns;
        color: $color_blue;
        &:hover:not(:active) {
            color: $color_blue_hover;
        }
        svg {
            width: 18px;
            height: 18px;
            stroke: #30B4FF;
        }
    }

    &__left {
        display: flex;
        flex-wrap: wrap;
        margin: -5px;
        width: 85%;

        @include below(1024px) {
            order: 3;
        }
    }

    &__left-pin {
        display: flex;
        align-items: center;
        margin: 5px;
        border: 1px solid #E2E3E5;
        border-radius: 6px;
        padding: 8px 12px;
        color: $color_dark;
        transition: background-color $trns;

        svg {
            width: 16px;
            height: 16px;
            margin-right: 8px;
        }
    }

    &__right {
        display: flex;
        align-items: center;
        margin-left: auto;

        @include below(1024px) {
            width: 100%;
            justify-content: space-between;
            order: 1px;
        }
    }

    &__data {
        font-size: 12px;
        line-height: 130%;
        color: #C4C4C4;
        margin-right: 20px;
    }

    &__right-link {
        svg {
            width: 24px;
            height: 24px;
        }
       
        
    }

    &__bottom {
        flex: auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;

        @include below(1024px) {
            order: 4;
            flex-direction: column;
            align-items: flex-start;
            justify-content: initial;
        }
    }

    &__order {
        @include below(1024px) {
            display: flex;
            justify-content: space-between;
        }

        @include below(540px) {
            padding-top: 16px;
            flex-direction: column;
            border-top: 1px solid #E2E3E5;
            width: 100%;
        }
    }

    &__order-btn {
        @include below(1024px) {
            order: 3;
            width: 100%;
            max-width: 225px;
        }

        @include below(420px) {
            max-width: 100%;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        font-weight: 600;
        font-size: 14px;
        line-height: 130%;
        color: $color_blue;
        transition: color $trns;
        margin-right: 24px;
        &:hover:not(:active) {
            color: $color_blue_hover;
        }
        
        @include below(1024px) {
            order: 2;
            margin-right: 0;
            margin-bottom: 10px;

            width: 100%;
            max-width: 225px;
            padding: 13px;
            justify-content: center;
            
            border: 1px solid currentColor;
            border-radius: 8px;
        }

        @include below(420px) {
            min-width: 100%;
        }

        svg {
            width: 18px;
            height: 18px;
            margin-right: 8px;
            stroke: #30B4FF;
        }
    }

    &__price {
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: 0.02em;
        margin-right: 40px;
        margin-left: auto;

        @include below(1024px) {
            margin-right: 15px;
            order: 1;
            margin-left: 0;
            margin-bottom: 16px;
        }
    }
}