@import "main-module";

.btn {
    font-family: $accentFont;
    font-weight: 600;
    font-size: 14px;
    line-height: 130%;
    color: $color_white;
    padding: 12px;
    background-color: $color_blue;
    transition: background-color $trns;
    border-radius: 8px;
    border: 1px solid $color_blue;
    text-align: center;
    
    &:hover:not(:active):not(.btn_empty):not(.btn_white) {
        background-color: #47BDFF;
    }

    &:focus:not(.btn_empty):not(.btn_white) {
        background-color: #0EA8FF;
        color: $color_white;
    }

    &:disabled {
        pointer-events: none !important;
        background-color: $color_white !important;
        background: #D9DEE0 !important;
    }

    &_empty {
        color: $color_blue;
        background-color: $color_white;
        border-color: currentColor;

        &:hover:not(:active) {
            color: #47BDFF;
            
        }
    }

    &_white {
        color: $color_dark;
        background-color: $color_white;
        border-color: currentColor;
        &:focus,
        &:hover:not(:active) {
            background-color: #F3F3F3;
        }
    }
}