@import "main-module";

.accordion {
    padding: 120px 0;

    @include below(1024px) {
        padding: 60px 0;
    }

    &__title {
        font-size: 18px;
        font-weight: 500;
        line-height: 130%;
        margin-bottom: 40px;

        @include below(1024px) {
           margin-bottom: 30px;
        }
    }
    
    &__wrap {
        padding: 30px;
        border-radius: 15px;
        border: 1px solid $color_grey_light;
        @include below(1024px) {
           padding: 20px;
           border-radius: 10px;
        }
    }
    
    &__item {
        padding: 30px 0;
        @include below(1024px) {
            padding: 20px 0;
        }
        &:first-child {
            padding: 0 0 30px;
            @include below(1024px) {
                padding: 0 0 20px;
            }
        }
        &:last-child {
            padding: 30px 0 0;

            @include below(1024px) {
                padding: 20px 0 0;
            }
        }
        &:not(:last-of-type) {
            border-bottom: 1px solid $color_grey_light;
        }
    }

    &__head {
        display: flex;
        justify-content: space-between;
        cursor: pointer;

        &_active {
            .accordion__icon {
                svg {
                    transform: rotate(180deg);
                }
            }
        }
    }

    &__head-title {
        font-size: 18px;
        font-weight: 500;
        line-height: 130%;
    }

    &__icon {
        margin-left: 10px;
        svg {
            width: 24px;
            height: 24px;
            transition: .3s linear;
        }
    }

    &__content {
        transition: .3s linear;

        &.accordion__content_open {
            max-height: auto;
            margin-top: 20px;
        }

        &:not(.accordion__content_open) {
            max-height: 0;
            overflow: hidden;
            opacity: 0;
        }
    }
}