@import "main-module";

.card-ads {
    padding: 30px;
    border-radius: 15px;
    border: 1px solid $color_grey_light;

    &__category {
        font-size: 12px;
        font-weight: 500;
        line-height: 130%;
        color: #2EB625;
        background-color: #E0F8DE;
        border-color: transparent;
    }

    @include below(1024px) {
        padding: 20px;
        border-radius: 10px;
    }
 
    &__flex {
        position: relative;
        display: flex;
        justify-content: space-between;
        @include below(479px) {
            flex-direction: column;
            position: relative;
        }
    }

    &__col {
        display: flex;
        flex-direction: column;

        &:first-of-type {
            width: 100%;
            max-width: 954px;
            margin-right: auto;
        }

        &:last-of-type {
            align-items: flex-end;
            margin-left: 30px;

            @include below(479px) {
               align-items: stretch;
               margin: 20px 0 0;
            }
        }
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -5px 20px;

        @include below(768px) {
           margin: 0 0 20px;
        }
    }

    &__row-item {
        display: flex;
        align-items: center;
        margin: 5px;
        padding: 8px 12px;
        color: $color_dark;
        font-size: 12px;
        font-weight: 500;
        line-height: 130%;
        letter-spacing: 0.24px;
        border-radius: 6px;
        border: 1px solid $color_grey_light;

        @include tooltip-template;
        &::before {
            white-space: nowrap;
            text-align: center;
            width: auto;
        }

        &_green {
            color: #2EB625;
            background-color: #E0F8DE;
            border-color: transparent;
        }

        &_orange {
            color: #FFAE30;
            background: #FFF3E0;
            border-color: transparent;
        }

        @include below(768px) {
            margin: 0;
            &:not(:first-of-type) {
                display: none;
            }
        }

        svg {
            width: 16px;
            height: 16px;
            margin-right: 8px;
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 600;
        line-height: 110%; 
        letter-spacing: 0.48px;

        @include below(768px) {
            font-size: 20px;
        }
    }

    &__text {
        color: #494949;
        font-size: 16px;
        line-height: 130%;
        margin: 16px 0 20px;
    }

    &__more {
        display: inline-flex;
        align-items: center;
        transition: text-decoration .3s linear;

        &:hover {
            text-decoration: underline;
        }
        svg {
            width: 18px;
            height: 18px;
        }
    }

    &__list {
        display: none;
        margin-bottom: 20px;

        @include below(768px) {
            display: block;
        }
    }

    &__list-item {
        display: flex;
        align-items: center;

        &:not(:last-of-type) {
            margin-bottom: 12px;
        }
        svg {
            width: 16px;
            height: 16px;
            margin-right: 8px;
        }

        span {
            font-size: 14px;
            line-height: 130%; 
            letter-spacing: 0.28px;

            &:first-of-type {
                color: $color_grey;
                margin-right: 8px;
            }
        }
    }

    &__bottom-text {
        color: #494949;
        font-size: 12px;
        line-height: 130%
    }

    &__date {
        color: #868686;
        font-size: 12px;
        line-height: 130%;
        @include below(479px) {
            position: absolute;
            top: 0;
            right: 0;
        }
    }

    &__btn {
        margin-top: auto;
        padding: 12px 20px;
    }

}