@import "main-module";

.filter-stock {
    margin-bottom: 32px;

    &__reset {
        display: inline-flex;
        align-items: center;
        margin-bottom: 32px;
        @include below(500px) {
            display: flex;
            margin-bottom: 30px;
        }
        
        span {
            color: $color_dark;
        }
        svg {
            width: 18px;
            height: 18px;
            margin-left: 12px;
        }
    }

    &__title {
        color: $color_dark;
        font-size: 16px;
        font-weight: 500;
        line-height: 130%;
        letter-spacing: 0.32px;
        margin-bottom: 32px;

        span {
            color: $color_blue;
        }

        &_modal {
            font-size: 24px;

            @include below(1024px) {
                font-size: 20px;
            }

            br {
                display: none;

                @include below(768px) {
                    display: block;
                }
            }
        }
    }

    &__form {
        width: 100%;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px;
        
        @include below(992px) {
            margin: -8px;
        }

        @include below(450px) {
            margin: -16px 0 0;
        }

        &_second {
            margin: 0 -12px;

            @include below(520px) {
                margin: -24px 0 0;
            }

            .filter-stock__label {
                width: calc(25% - 24px);    
                margin: 0 12px;

                @include below(1200px) {
                    width: calc(33% - 24px);
                }

                @include below(992px) {
                    width: calc(50% - 24px);
                }

                @include below(520px) {
                    width: 100%;
                    margin: 24px 0 0;
                }
            }
        }

    }

    &__label {
        width: calc(25% - 16px);    
        margin: 0 8px;

        @include below(1200px) {
            width: calc(33% - 16px);
        }

        @include below(992px) {
            width: calc(50% - 16px);
            margin: 8px;
        }

        @include below(450px) {
            width: 100%;
            margin: 16px 0 0;
        }
    }

    &__label-search {
        position: relative;
        width: calc(62% - 16px);
        margin: 0 8px;

        @include below(1200px) {
            width: calc(33% - 16px);
        }

        @include below(1400px) {
            width: calc(25% - 16px);
        }

        @include below(992px) {
            width: calc(50% - 16px);
            margin: 8px;
        }

        @include below(450px) {
            width: 100%;
            margin: 16px 0 0;
        }
    }

    &__search {
        width: 100%;
        border: 1px solid #e2e3e5;
        border-radius: 8px;
        height: 48px;
        display: flex;
        align-items: center;
        transition: border-color 0.3s linear;
        padding: 0 20px;
        background-color: #fff;
        font-size: 14px;
        line-height: 130%;
        color: #868686;

        &:hover {
            border-color: $color_blue;
        }
    }

    &__icon {
        position: absolute;
        top: 10px;
        right: 10px;

        svg {
            width: 24px;
            height: 24px;
        }
    }

    &__btn {
        height: 48px;
        width: calc(13% - 16px);
        margin: 0 8px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include below(1400px) {
            width: calc(25% - 16px);
        }

        @include below(1200px) {
            width: calc(33% - 16px);
        }

        @include below(992px) {
            width: calc(50% - 16px);
            margin: 8px;
        }

        @include below(450px) {
            width: 100%;
            margin: 16px 0 0;
        }

        svg {
            flex-shrink: 0;
            width: 18px;
            height: 18px;
            margin-right: 4px;
        }
    }

}