@import "main-module";

.calculator {
    padding: 60px 0 120px;
    overflow: hidden;

    @include below(1024px) {
        padding: 30px 0 60px;
    }

    &__row {
        position: relative;
        padding: 30px;
        background-color: $color_blue;
        border-radius: 15px;

        @include below(768px) {
            padding: 20px;
            border-radius: 10px;
        }
    }

    &__content {
        position: relative;
        z-index: 100;
        color: $color_white;
        max-width: 630px
    }

    &__title {
        font-size: 40px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.8px;

        @include below(1240px) {
            font-size: 35px;
        }

        @include below(768px) {
            font-size: 26px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
        margin: 16px 0 24px;
    }

    &__img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;

        @include below(1400px) {
            opacity: 0.4;
        }

        @include below(992px) {
            height: 226px;
            opacity: 1;
        }

        @include below(368px) {
            height: 200px;
        }

        img {
            object-fit: contain;
            width: 100%;
            height: 100%;
            border-top-right-radius: 15px;
            border-bottom-right-radius: 15px;
        }
    }

    &__btn {
        padding: 12px 20px;
        border: none;

        @include below(479px) {
            width: 100%;
        }
    }
}