@import "main-module";

.product-content {
    padding: 30px 0 40px;
    @include below(420px) {
        padding: 0 0 20px;
    }

    &.product-content_second {
        padding: 60px 0 120px;

        @include below(1024px) {
            padding: 30px 0 60px;
        }
    }

    &__tab {
        
        display: flex;
        align-items: center;
        margin-bottom: 32px;

        
        @include below(420px) {
            margin-bottom: 30px;
        }

        .btn {
            transition: background-color $trns, color $trns, font-weight $trns;
            @include fontWeightToBold;

            &.btn_empty {
                font-weight: 400;
                color: $color_dark;
                border-color: $color_blue;
            }
         

            &:not(:last-child) {
                margin-right: 10px;
            }

            @include below(420px) {
                flex: auto;
            }

            &:hover,
            &:active {
                font-weight: 600;
                color: $color_white;
                background-color: $color_blue;
            }
        }
    }

    &__reviews,
    &__description {
        &:not(.is-active) {
            display: none;
        }
    }


    &__reviews {
        &-row {
            display: flex;
            flex-wrap: wrap;
            margin: -12px;
    
            @include below(1024px) {
                margin: -10px;
            }
    
            @include below(540px) {
                margin: -5px;
            }
        }
    
        &-item {
            position: relative;
            width: calc(25% - 24px);
            margin: 12px;
    
            @include below(1024px) {
                width: calc(33.333% - 20px);
                margin: 10px;
            }
    
            @include below(540px) {
                width: calc(50% - 10px);
                margin: 5px;
            }
        }
    
        &-icon {
            position: absolute;
            
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            svg {
                width: 60px;
                height: 60px;
    
                @include below(1024px) {
                    width: 40px;
                    height: 40px
                }
            }
        }
    
        &-name {
            position: absolute;
            left: 30px;
            bottom: 30px;
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: $color_dark;
    
            @include below(1024px) {
                left: 20px;
                bottom: 20px;
            }
    
        }
    }

    &__description {
        
        &-flex {
            display: flex;

            @include below(1024px) {
                flex-wrap: wrap;
                flex-direction: column;
            }

            @include below(768px) {
                display: block;
                position: relative;

                &:not(.is-show) {
                    max-height: 1200px;
                    overflow: hidden;
                }

                &.is-hidden {
                    &::after {
                        content: "";
                    }
                }

                &::after {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 100px;
                    background: linear-gradient(180deg, rgba(20,166,213,0) 0%, rgba(255,255,255,1) 71%);
                }
            }
        }
    }

    &__more {
        margin-top: 10px;
        display: inline-block;

        @include above(769px) {
            display: none;
        }

        @include below(420px) {
            width: 100%;
        }
    }

    &__chapters {
        width: 100%;
        max-width: 312px;

        h2 {
            font-weight: 500;
            font-size: 18px;
            line-height: 130%;
            margin-bottom: 16px; 

            @include below(768px) {
                font-size: 16px;
                margin-bottom: 8px;
            }
        }

        ul {
          
        }

        ul li {
            font-size: 14px;
            line-height: 130%;
            letter-spacing: 0.02em;
            margin-bottom: 10px;
            display: flex;
            align-items: flex-end;
            justify-content: space-between;
            list-style-type: disc;
            span {
                margin-left: 10px;
            }
            strong {
                flex: auto;
                display: flex;
                align-items: flex-end;
                justify-content: space-between;

                font-weight: 500;
            }
        }

        a {
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            letter-spacing: 0.02em;
            transition: color $trns;
            color: $color_blue;
            display: flex;
            align-items: center;
            svg {
                margin-left: 2px;
                width: 18px;
                height: 18px;
            }

            &:hover:not(:active) {
                color: $color_blue_hover;
            }
        }
    }

    &__main {
        @include above(769px) {
            margin-right: 24px;
        } 

        &_second {
            @include above(769px) {
                margin-right: 0;
            }     
        }
        h2 {
            font-weight: 500;
            font-size: 18px;
            line-height: 130%;
            margin-bottom: 16px; 

            @include below(768px) {
                font-size: 16px;
                margin-bottom: 8px;
            }
        }

        p {
            font-size: 16px;
            line-height: 130%;
            margin-bottom: 24px;
            @include below(768px) {
                margin-bottom: 20px;
            }

            &:not(:last-child) {
                margin-bottom: 16px;
            }
        }

        ol {
            padding-left: 0;
            display: flex;
            flex-direction: column;
            list-style-position:inside;
        }

        ul {
            list-style-type: disc;
            padding-left: 20px;
            margin-bottom: 24px;
        }

        ol li {
            font-size: 16px;
            line-height: 130%;
            text-align: left;
            &:not(:last-child) {
                margin-bottom: 16px;
                @include below(768px) {
                    margin-bottom: 10px;
                }
            }
        }
    }
}