@import "main-module";


.content-table {
    @include block-padding;

    
    &_second {
        padding: 0;

        @include below(1024px) {
            padding: 0;
        }

        .content-table__list_full {
            .content-table__item {
                @include below(768px) {
                    &:not(:nth-child(-n+20)) {
                        display: block;
                    }
                }
            }
        }
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px; 

        @include below(1024px) {
            margin-bottom: 30px;
        }
    }
    
    &__title {
        @include block-title($isMargin: false);
    }

    &__head-link {
        @include head-link;
        margin-left: 24px;
    }

    &__container {
        padding: 30px;
        border: 1px solid #E2E3E5;
        border-radius: 15px;
        display: flex;
        flex-direction: column;

        @include below(768px) {
            padding: 0;
            border: 0;
        }
    }

    &__tab {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        height: 54px;
        overflow: hidden;
        margin: -7px;
        
        @include below_and_above(1240px,768px) {
            flex-wrap: wrap;
        }

        @include below(768px) {
            flex-wrap: nowrap;
            overflow-x: auto;
            height: 44px;
        }
    }

    &__tab-link {
        font-weight: 400;
        background-color: $color_white;
        color: $color_dark;
        margin: 7px;
        border-radius: 6px;
        transition: background-color .3s linear, font-weight .4s ease-in-out, color .3s linear;

        @include fontWeightToBold;

        &.content-table__tab-link_active,
        &:hover,
        &:active {
            font-weight: 600;
            color: $color_white;
            background-color: $color_blue;
        }

        &:focus {
            background-color: $color_white;
        }

        &.content-table__tab-link_hide {
            @include above(769px) {
                display: none;
            }
        }

        &:last-of-type:not(.content-table__tab-link_show) {
            display: none;
            
        }

        &.content-table__tab-link_show {
            width: 50px;
            @include below(768px) {
                display: none;
            }
        }

        @include below(1240px) {
            margin: 6px 6px;
        }

        @include below(768px) {
            margin: 0 6px;
            white-space: nowrap;
        }
    }

    &__block {
        margin-top: 20px;
        .content-table__list:not(.content-table__list_active) {
            display: none;
        }
    }

    &__list {
        column-count: 2;
        column-gap: 40px;

        @include below(768px) {
            column-count: 1;
            column-gap: 0;
        }
        
        &.content-table__list_second {
            @include above(1201px) {
                column-count: 4;
            }
            @include below_and_above(1200px, 1025px) {
                column-count: 3;
            }

            @include below_and_above(1024px, 769px) {
                column-count: 2;
            }
        }

        &.content-table__list_third {
            @include below(768px) {
                .content-table__link {
                    align-items: flex-end;
                }
                
                .content-table__text {
                    max-width: 215px;
                }
            }  
            .content-table__item {
                @include below(768px) {
                    &:not(:nth-child(-n+10)) {
                        display: block;
                    }
                }
            }
            @include below(1200px) {
                column-count: 1;
                column-gap: 0;
            }

        }
    }

    &__item {
        margin: 0 -10px -10px;
        
        padding-bottom: 6px;

        &[data-limit="20"] {
            @include above(769px) {
                &:not(:nth-child(-n+20)):not(.is-show-all) {
                    display: none;
                }
            }
        }

        &[data-limit="40"] {
            @include above(769px) {
                &:not(:nth-child(-n+40)):not(.is-show-all) {
                    display: none;
                }
            }
        }

        @include below(768px) {
            &:not(:nth-child(-n+10)):not(.is-show-all) {
                display: none;
            }
        }

        &.content-table__item_show-more {
            display: none;
        }
    }

    &__link {
        display: flex;
        justify-content: space-between;
        transition: background-color .3s linear;
        padding: 10px;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: 0.02em;
        border-radius: 8px;

        &:hover:not(:active) {
            background-color: #ECF7FF;
        }

        &.content-table__link_last {
            display: inline-block;
            padding-left: 12px;
            padding-right: 12px;
        }

        &:not(.content-table__link_last) {
            &::after {
                content: "";
            }
        }

        &::after {
            display: block;
            border-bottom:  1px dashed #E2E3E5;
            margin: 0 10px;
            flex: auto;
            order: 1;
        }

        [data-dots] {
            @include below(768px) {
                display: none;
            }
        }

        &::after {
            .content-table__list.content-table__list_null & {
                @include above(769px) {
                    content: none;
                }
            }
        }
    }

    &__link-more {
        display: flex;
        align-items: center;
        color: $color_blue;
        font-weight: 600;
        font-size: 14px;
        line-height: 130%;

        letter-spacing: 0.01em;

        @include above(769px) {
            display: none;
        }

        svg {
            width: 18px;
            height: 18px;
            margin-left: 2px;
        }
    }

    &__text {
        color: $color_blue;
    }

    &__numb {
        order: 2;
        color: $color_dark;
        white-space: nowrap;
        .content-table__list.content-table__list_second & {
            color: $color_blue;
        }

        .content-table__list.content-table__list_null & {
            @include above(769px) {
                display: none;
            }
        }
    }

    &__btn {
        display: none;
        text-align: center;

        @include below(768px) {
            display: inline-block;
            align-self: center;
            margin-top: 25px;
            width: 100%;
            max-width: 225px;
        }

        @include below(479px) {
            max-width: 100%;
        }
    }


}