@import "main-module";

.tools {
    @include block-padding;

    &__main-title {
        @include block-title;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 24px;

        @include below(1024px) {
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: repeat(2, 1fr);
        }

        @include below(768px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(3, 1fr);
            gap: 10px;
        }
    }

    &__item {
        overflow: hidden;
        position: relative;
        display: flex;
        flex-direction: column;
        color: $color_dark;
        padding: 30px;
        border: 1px solid #E2E3E5;
        border-radius: 15px;
        transition: color .1s linear, border-color .1s linear;

        @include below(768px) {
            padding: 20px;
            border-radius: 10px;
        }

        &:hover {
            border-color: transparent;
            color: $color_white;
            .tools__item-img {
                opacity: 1;
            }

            svg {
                stroke: #fff;
            }
        }

        &:nth-child(1) {
            grid-column: 1/2;
            grid-row: 1/2;

            @include below(1024px) {
                grid-column: 1/2;
                grid-row: 1/2;
            }

            @include below(768px) {
                grid-column: 1/2;
                grid-row: 1/2;
            }
        }

        &:nth-child(2) {
            grid-column: 1/2;
            grid-row: 2/3;

            @include below(1024px) {
                grid-column: 2/3;
                grid-row: 1/2;
            }

            @include below(768px) {
                grid-column: 2/3;
                grid-row: 1/2;
            }
        }

        &:nth-child(3) {
            grid-column: 2/3;
            grid-row: 1/3;

            @include below(1024px) {
                grid-column: 3/4;
                grid-row: 1/2;
            }

            @include below(768px) {
                grid-column: 1/2;
                grid-row: 2/3;
            }
        }
        

        

        &:nth-child(6) {
            grid-column: 3/5;
            grid-row: 2/3;

            
            @include below(1024px) {
                grid-column: 3/4;
                grid-row: 2/3;
            }

            @include below(768px) {
                grid-column: 2/3;
                grid-row: 3/4;
            }
        }
    }

    &__item-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        opacity: 0;
        border-radius: 15px;
        transition: opacity .4s linear;
        
        img {
            height: 100%;
            object-fit: cover;
        }
    }

    &__item-title {
        margin-top: 40px;
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: 0.02em;

        @include below(1240px) {
           font-size: 20px;
        }

        @include below(768px) {
            font-size: 16px;
            line-height: 130%;
        }
    }

    &__item-text {
        margin-top: 20px;
        font-size: 16px;
        line-height: 130%;

        @include below(768px) {
            display: none;
        }
    }

    &__item-btn {
        margin-left: auto;
        margin-bottom: auto;
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 12px;
        line-height: 130%;
        letter-spacing: 0.02em;

        @include below(1240px) {
            top: 20px;
            right: 20px;
        }

        svg {
            width: 18px;
            height: 18px;
            stroke: #404040;
            transition: stroke .4s linear;
        }
    }
}