@mixin block-title($isMargin: true) {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;

    @if $isMargin {
        margin-bottom: 40px;

        @include below(1024px) {
            margin-bottom: 30px;
        }
    }
}

@mixin head-link {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 12px;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: $color_blue;
    transition: color .3s linear,
    stroke .3s linear;

    @include below(768px) {
        display: none;
    }

    &:hover {
        color: #47BDFF;

        svg {
            stroke: #47BDFF;
        }
    }

    &:active {
        color: $color_blue;

        svg {
            stroke: #0EA8FF;
        }
    }


    svg {
        stroke: #30b4ff;
        width: 18px;
        height: 18px;
    }
}

@mixin block-padding() {
    padding: 60px 0;

    @include below(1024px) {
        padding: 30px 0;
    }

}

@mixin below($width) {
    @media (max-width: $width) {
      @content;
    }
}

@mixin above($width) {
    @media (min-width: $width) {
        @content;
    }
}

@mixin below_and_above($max, $min) {
    @media (max-width: $max) and (min-width: $min) {
        @content;
    }
}


@mixin scrollbar($color_scroll, $color_track: transparent, $w: 10px, $h: 10px) {
    //полоса прокрутки
    &::-webkit-scrollbar {
        overflow: visible;
        width: $w;
        height: 10px;
        background-color: $color_track;
    }

    //фоновая часть
    &::-webkit-scrollbar-track {
        border: 0 solid transparent;
        background-color: transparent;
        background-clip: padding-box;
    }

    //кнопки вверх и вниз
    &::-webkit-scrollbar-button {
        width: 0;
        height: 0;
    }

    //ползунок
    &::-webkit-scrollbar-thumb {
        min-height: 28px;
        border: 2px solid transparent;
        background-color: transparentize($color_scroll, .3);
        background-clip: padding-box;
        cursor: pointer;

        &:hover {
            background-color: transparentize($color_scroll, .2);
        }

        &:active {
            background-color: $color_scroll;
        }
    }
}

@mixin tooltip-template {
    position: relative;

    &:hover {
        &::after,
        &::before {
            opacity: 1;
            visibility: visible;
        }
    }

    &[data-tooltip] {
        &::before {
            content: attr(data-tooltip);
        }
        &::after {
            content: "";
        }
    }  
    
    &::before,
    &::after {
        position: absolute;
        top: 0;
        left: 50%;
        color: $color_white;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.2s linear;
        font-size: 12px;
    }

    &::before {
        background-color: rgba(0, 0, 0, 0.8);
        border-radius: 0.5em;
        width: 10rem;
        padding: 0.5rem;
        transform: translate(-50%, calc(-100% + 12px * -1));
    }
    
    &::after {
        
        width: 12px;
        aspect-ratio: 1;
        background-color: rgba(0, 0, 0, 0.8);
        clip-path: polygon(44% 100%, 0 44%, 100% 44%);
        transform: translate(-50%, calc(-100% + 12px / 2 * -1));
    }
}

@mixin fontWeightToBold {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    &::after {
        content: attr(data-text);
        content: attr(data-text) / "";
        height: 0;
        visibility: hidden;
        overflow: hidden;
        user-select: none;
        pointer-events: none;
        font-weight: 600;
      
        @media speech {
          display: none;
        }
    }
}