@import "main-module";

.service-block {
    padding: 60px 0 120px;
    @include below(1024px) {
        padding: 30px 0 60px;
    }
    &__title {
        @include block-title;
    }

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: 30px;
        border: 1px solid #E2E3E5;
        border-radius: 15px;
        margin-bottom: 20px;
        overflow-wrap: break-word;

        @include below(1024px) {
            flex-direction: column;
            align-items: flex-start;
        }

        @include below(768px) {
            padding: 20px;
            border-radius: 10px;
        }


        &:last-of-type {
            margin-bottom: 40px;

            @include below(479px) {
               margin-bottom: 30px;
            }
        }
    }

    &__item-title {
        font-weight: 600;
        font-size: 24px;
        line-height: 110%;
        letter-spacing: 0.02em;
        margin-right: 40px;
        min-width: 320px;

        @include below(1240px) {
            font-size: 20px;
        }

        @include below(1024px) {
            margin-right: 0;
            min-width: initial;
        }

        
    }

    &__item-text {
        max-width: 400px;
        margin-left: auto;
        margin-right: 40px;
        font-size: 14px;
        line-height: 130%;
        
        @include below(1024px) {
            margin: 16px 0 24px;
        }
    }

    &__item-btn {
        @include below(479px) {
            width: 100%;
        }
    }

    &__btn {
        
        display: block;
        margin: 0 auto;
        max-width: 145px;

        @include below(768px) {
            max-width: 225px;
        }

        @include below(479px) {
            max-width: 100%;
        }
    }
}